const createPropName = (oldProp) => {
  let newProp = oldProp.slice();
  return newProp.replace(/\W+/g, '-').toLowerCase();
}

createPropName('somethi sdf ed fsdf d ')

const debugAttrs = (attrs) => {
  const result = {},
        valsArray = Object.keys(attrs),
        keysArray = Object.keys(attrs) .map( val => createPropName(val) );
  for (let i = 0; i < keysArray.length; i++){
    result[keysArray[i]] = valsArray[i];
  }
  return result;
}

export { debugAttrs };



